import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader'


let hdrTextureURL = new URL('../hdr/微信图片_20221127170849.hdr', import.meta.url)
let hdrTextureURL1 = new URL('../hdr/微信图片_20221127171330.hdr', import.meta.url)
let hdrTextureURL2 = new URL('../hdr/微信图片_20221127171334.hdr', import.meta.url)

let scene = new THREE.Scene()


let width = 1350;
let height = 800;
let camera = new THREE.PerspectiveCamera(
    75,
    width/height,
    1,
    1000
);
camera.position.set( 200, 200, 200 )
camera.lookAt( 0, 0, 0 )
scene.add( camera );



const renderer = new THREE.WebGL1Renderer(
    {
     alpha: true
    }
);
renderer.setSize( width, height );

renderer.physicallyCorrectLights = true;

renderer.outputEncoding = THREE.sRGBEncoding
renderer.toneMapping = THREE.ACESFilmicToneMapping
renderer.toneMappingExposure = 1.8

let loader = new RGBELoader()
loader.load( hdrTextureURL, function( texture ) {
    texture.mapping = THREE.EquirectangularReflectionMapping
    scene.background = texture
    // scene.environment = texture
} )




   function changeVr00 () {
        console.log("美露莘")
        scene.background = ''
        loader.load( hdrTextureURL, function( texture ) {
        texture.mapping = THREE.EquirectangularReflectionMapping
        scene.background = texture        
         } )
    }
    
   function changeVr01 () {
        console.log("芭万希")
        scene.background = ''
        loader.load( hdrTextureURL1, function( texture ) {
        texture.mapping = THREE.EquirectangularReflectionMapping
        scene.background = texture        
         } )
    }

   function changeVr02 () {
        console.log("巴格斯特");
        scene.background = '';
        loader.load( hdrTextureURL2, function( texture ) {
        texture.mapping = THREE.EquirectangularReflectionMapping
        scene.background = texture        
         } )
    }


function render() {
  renderer.render(scene, camera)
  requestAnimationFrame(render)
}
render()


let controls = new OrbitControls( camera, renderer.domElement )
controls.enableZoom = true


export { renderer, changeVr00, changeVr01, changeVr02 }
