<template>
<div class="inventory">  
        <input type="text" placeholder="种名 目 科" v-model="inputText" @input="searchAction($event)" />
        <button @click="clear()">清除</button><div>{{ insectNumber.length}}</div>
    <table class="table">      
        <tr>
            <th>中文名</th>
            <th>拉丁名</th>
            <th>分类地位</th>
            <th class="number">库存数量</th>
            <th class="tdImg">图片</th>
        </tr>  
        <div v-show="(searchResult.length > 0 && searchResult.length < 500)" style="color:red">{{ searchResult.length}}</div>
        <tr v-for="item of searchResult" :key="item.content" style="color:red">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number"></td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }">{{item.img}}</td>
        </tr>    
        <div class="ordernumber">半翅目: {{ Hemiptera.length}}种</div>
        <tr v-for="item of Hemiptera" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number"></td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }">{{item.img}}</td>
        </tr>
        <div class="ordernumber">鞘翅目: {{ Coleoptera.length}}种</div>
        <tr v-for="item of Coleoptera" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number">{{item.number}}</td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"></td>
        </tr>
        <!-- 鳞翅目 -->
        <div class="ordernumber">鳞翅目: {{ Lepidoptera.length}}种</div>
        <tr v-for="item of Lepidoptera" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number">{{item.number}}</td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"></td>
        </tr>
        <!-- 膜翅目 -->
        <div class="ordernumber">膜翅目: {{ Hymenoptera.length}}种</div>
        <tr v-for="item of Hymenoptera" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number">{{item.number}}</td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"></td>
        </tr>
        <!-- 螳螂目 -->
        <div class="ordernumber">螳螂目: {{ Mantodea.length}}种</div>
        <tr v-for="item of Mantodea" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number">{{item.number}}</td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"></td>
        </tr>
        <!-- 直翅目 -->
        <div class="ordernumber">直翅目: {{ Orthoptera.length}}种</div>
        <tr v-for="item of Orthoptera" :key="item.ldm">
            <td>{{item.zwm}}</td>
            <td>{{item.ldm}}</td>
            <td>{{item.order}}-{{item.family}}</td>
            <td class="number">{{item.number}}</td>
            <td class="img" :style="{ 'background-image': 'url(' + item.imgUrl + ')' }"></td>
        </tr>

    </table>
    
  
</div>
</template>
<script>
import data from '@/data/searchData'
export default {
    name: 'InsectInventory',
    data() {
        return {
           data,
           searchResult: [],
           inputText: '',          
        }
    },
    computed:{
        insectNumber() {
            return this.data.filter(function (list) {
                return list.condition01 == "库存标本"
            })
        },
        Hemiptera(){
            return this.data.filter(function(list) {
                return list.order == '半翅目' && list.condition01 == "库存标本"
            })
        },
        Coleoptera(){
            return this.data.filter(function(list) {
                return list.order == '鞘翅目' && list.condition01 == "库存标本"
            })
        } ,     
        Lepidoptera(){
            return this.data.filter(function(list) {
                return list.order == '鳞翅目' && list.condition01 == "库存标本"
            })
        } ,     
        Hymenoptera(){
            return this.data.filter(function(list) {
                return list.order == '膜翅目' && list.condition01 == "库存标本"
            })
        } ,     
        Mantodea(){
            return this.data.filter(function(list) {
                return list.order == '螳螂目' && list.condition01 == "库存标本"
            })
        } ,     
        Orthoptera(){
            return this.data.filter(function(list) {
                return list.order == '直翅目' && list.condition01 == "库存标本"
            })
        } ,   

    },
    methods: {
        searchAction(e) {
            let inputValue = e.target.value
            this.searchResult = this.data.filter((item) => {
                if (item.content.includes(inputValue) && item.condition01 == '库存标本') {
                    return item
                } 
            })
        },
        clear() {
            this.searchResult= []
            this.inputText = ''
        }
    },
}
</script>
<style scoped>
.table{
    border:1px solid black;
    border-collapse: collapse;
    text-indent: 2em;
}
th{
border: 1px solid black;
width:18vw;
height:5vh;
font-size: 20px;
font-weight: bold;
}
td{
border: 1px solid black;
width: 10vw;
height: 10vh;
font-size: 20px;
}
.number{
    width:8vw;
}
.tdImg{
    width:15vh
}
.img{
    width:15vh;
    background-size: 145px;
    background-repeat: no-repeat
    
}
.ordernumber{
    font-size: 16px;
    padding:5px 0;
    font-weight: bold;
}
</style>