let width = 1000;
let height = 678;
let camera = new THREE.PerspectiveCamera(
    60,
    width/height,
    1,
    300
);
camera.position.set( 30, 30, 30 )
camera.lookAt( 0, 0, 0 )
scene.add( camera );



const renderer01 = new THREE.WebGL1Renderer(
    {
     alpha: true
    }
);
renderer01.setSize( width, height );

renderer01.physicallyCorrectLights = true;

renderer01.outputEncoding = THREE.sRGBEncoding
renderer01.toneMapping = THREE.ACESFilmicToneMapping
renderer01.toneMappingExposure = 1.8

let loader = new RGBELoader()
loader.load( hdrTextureURL, function( texture ) {
    texture.mapping = THREE.EquirectangularReflectionMapping
    scene.background = texture
    scene.environment = texture
} )


function changeBac01 (){
    scene.background = new THREE.Color(0x000000)
}
function changeBac02 (){
    scene.background = new THREE.Color(0x808080)
}
function changeBac03 (){
loader.load( hdrTextureURL, function( texture ) {
    texture.mapping = THREE.EquirectangularReflectionMapping
    scene.background = texture
} )
}



function render() {
  renderer01.render(scene, camera)
  requestAnimationFrame(render)
}
render()


let controls = new OrbitControls( camera, renderer01.domElement )
controls.enableZoom = true



