<template>
    <div >
        <form>
            <div class="inputBox">
               <input ref="inputValue" class="input"  type="text" v-model="inputText" placeholder="请输入昆虫名称" @input="searchAction($event)"/>
               <div class="clearInput" @click="clearInput"></div>
            </div>
            <table>
                <div class="ull">
                    <!-- <li v-for="data in newDatalist" @click="goPage(data.id)" :key="data.id" class="searchLi">{{ data.content }}</li> -->            
            
                        <router-link  
                        v-for="data,index in searchResult" :key="index"                         
                         :to="`/searchDetail/${data.zwm}`"
                        class="searchLi"
                        @click="clearInput"                     
                      >
                      <div>{{ data.zwm }}</div>
                      <div>{{ data.ldm }}</div>
                    </router-link>
                    
           
                </div>
            </table>
        </form>
    </div>
</template>
<script>
import searchData from '@/data/searchData'
// import inventoryData from '@/data/InsectInventory'

export default {
  
    name: 'SearchBar3',
    data() {
        return {
              searchData,
            //   inventoryData,
              searchResult: [],
              inputText:"",
    
        }
    },  
    methods: {
        searchAction(e){
             const inputValue = e.target.value
             this.searchResult = this.searchData.filter((item) => {
                if( item.content.includes(inputValue)) {
                    return item
                }                
              })      
            // this.searchResult = this.inventoryData.filter((item) => {
            //     if( item.searchString.includes(inputValue)) {
            //         return item
            //     } 
            //   })
                //  || item.searchString.includes(inputValue)
           },
        clearInput () {

            this.searchResult = []
            this.inputText = ""        
        }   
        
    },
   


}

</script>
<style scoped>
input::-webkit-input-placeholder {
    /* WebKit browsers */

    padding-left: 10px;
    box-sizing: border-box;
}


.input {
    border:none;
    width:80%;
    font-size: 17px;
    padding-left: 20px;
    color: brown;
    background-color: rgb(187, 196, 196);
  
}
.inputBox{
    margin: 20px 0 20px 0;
    display:flex;
    border: 3px solid rgb(102, 100, 100);
    border-radius: 20px;
    background-color: rgb(187, 196, 196);
    padding:7px 3px;
}
.clearInput{
    width:20px;
    height:20px;
    /* margin:5px 0 0 0; */
    background-image: url('@/assets/icon/清除.jpg');
    background-size: 20px;
    margin:auto;
}

input {
    color: brown;
    /* font-size: 10px; */
    box-sizing: border-box;
    outline: none;
}

.ull {
    background-color:aliceblue;
    padding: 5px;
    display:flex;
    flex-direction: column;
    height: 350px;
    overflow: hidden;
}

.searchLi {
    border: 1px solid black;
    border-radius: 10px;
    margin:5px 0;
    padding: 5px;
    font-size: 16px;
    top:10px;
    line-height: 24px;
    height:60px;
    color: rgb(0, 0, 0);
    z-index: 99;
}
</style>